#footer {
    position: relative;
    padding-top: 80px;
    background: #0F0B25;

    .title {
        font-family: 'Champ';
        font-weight: 900;
        font-size: 40px;
        line-height: 47px;
        letter-spacing: 0.02em;
        color: #FFFFFF;
    }

    p {
        padding-top: 30px;
        padding-bottom: 30px;
        font-family: 'Poppins';
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 0.01em;
        color: #C3C2CF;
    }

    .btn-say-hello {
        padding: 16px 25px;
        gap: 12px;
        background: #613AD2;
        border-radius: 40px;
        font-family: "Poppins";
        font-weight: 700;
        font-size: 18px;
        line-height: 27px;
        letter-spacing: 0.02em;
        color: #ffffff;

        img {
            margin-left: 12px;
        }
    }

    .nav {
        margin-bottom: 24px;

        .nav-item {
            .nav-link {
                font-family: 'Poppins';
                font-weight: 400;
                font-size: 14px;
                line-height: 21px;
                color: #E0DFEA;
            }
        }
    }

    .menu {
        .nav-link {
            padding: 0px 20px;

            &:last-child {
                padding-right: 0;
            }
        }
    }

    .social {
        .nav-link {
            padding: 0px 10px;

            &:last-child {
                padding-right: 0;
            }
        }
    }

    .mail-to {
        margin-top: 40px;
        text-align: right;

        p {
            padding-top: 0;
            padding-bottom: 0;
            font-family: 'Poppins';
            font-weight: 600;
            font-size: 16px;
            line-height: 16px;
            color: #9896AE;
        }

        a {
            font-family: 'Poppins';
            font-weight: 700;
            font-size: 20px;
            line-height: 30px;
            letter-spacing: 0.02em;
            text-decoration-line: underline;
            color: #FFFFFF;
        }
    }

    .copyright {
        padding-top: 77px;
        padding-bottom: 30px;

        .left {
            h3 {
                font-family: 'Poppins';
                font-weight: 700;
                font-size: 24px;
                line-height: 26px;
                letter-spacing: -0.04em;
                color: #FFFFFF;
    
                span {
                    font-weight: 400;
                    font-size: 15px;
                    line-height: 24px;
                    letter-spacing: 0.01em;
                    color: #9896AE;
                    margin-left: 16px;
                }
            }
        }

        .right {
            a {
                font-family: 'Poppins';
                text-decoration: none;
                font-weight: 400;
                font-size: 14px;
                line-height: 21px;
                color: #E0DFEA;
            }

            img {
                margin-left: 30px;
            }
        }
    }
}

@media (min-width: 320px) and (max-width: 574px) {
    #footer {
        padding-top: 50px;

        .title {
            line-height: 34px;
            letter-spacing: 0.03em;

            h1 {
                margin-bottom: 0;
            }
        }

        p {
            padding-top: 20px;
            padding-bottom: 20px;
            margin-bottom: 0;
        }

        .btn-say-hello {
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 16px;
            line-height: 19px; 

            img {
                margin-left: 0;
            }
        }

        .menu {
            display: none;
        }

        .social {
            padding-top: 30px;
            justify-content: start !important;

            .nav-item {
                &:first-child {
                    width: 100%;
                    margin-bottom: 14px;
                }
            }
        }

        .mail-to {
            margin-top: 0;
            text-align: left;

            a {
                font-size: 16px;
                line-height: 24px;
            }
        }

        .copyright {
            padding-top: 40px;

            .left {
                h3 {
                    margin-bottom: 0;

                    span {
                        display: block;
                        margin-left: 0;
                        margin-top: 16px;
                        font-size: 14px;
                        line-height: 24px;
                    }
                }
            }

            .right {
                a {
                    margin-top: -100px;
                }

                img {
                    display: none;
                }
            }
        }
    }
}

